label {
	display: none;
	}		
#toggle{
	display: none;
	}
.nav-row{
	display: flex;
    justify-content: space-between;
    margin: 2% 10%;
    }
.nav-left{
	display: flex;
    }
.logo{
	background-repeat: no-repeat;
	padding-top: 0;
    }
.nav-menu{
	display: flex;
	justify-content: center;
	list-style-type: none;
	align-items: center;
	}
.responsive-home{
	display: none;
}
.nav-menu li{
	margin-left: 18px;
    }
.nav-menu li a{
	text-decoration: none;
	color:#000;
	opacity: 0.3;
	font-weight: 700;
    }
.nav-menu li a:hover{
	color: #000;
	opacity: 0.9;
    }
.nav-right .nav-right-menu{
	display: flex;
	justify-content: center;
	align-items: center;
    } 
.nav-right-menu{
	list-style-type: none;
    }
.nav-right-menu li{
	margin-left: 18px;
    }
.nav-right-menu li a{
	text-decoration: none;
	font-weight: 700;
	}
.nav-right-menu li:nth-child(1) a{
	color: #000;
	opacity: 0.3;
    }
.nav-right-menu li:nth-child(1):hover a {
	color:#000;
	opacity: 0.9;
    }
.nav-right-menu li:nth-child(2){
	background-color: #0099ff;
	border-radius: 20px;
	padding:5px 15px;
    }
.nav-right-menu li:nth-child(2) a{
	color: #fff;
    }
.nav-right-menu li:nth-child(2):hover a{
	color: #fff;
    }
.nav-right-menu li:nth-child(2):hover{
	background-color: #48dbfb;
	border-radius: 20px;
	padding:5px 15px;
    }
.nav-line{
	height: 1px ;
	width: 100%;
	background-color: #f0f0f0;
	display: none;
	opacity: 0.3;
    }
    
/*Media Queries*/
@media screen and (max-width: 900px)
{	
	label {
        margin: 3% 5%;
		display: block;
		cursor: pointer;
		text-align: right;
		font-size: 35px;
		justify-content: space-between;
		background-image: url('../../assets/images/rdre.png');
		background-repeat: no-repeat;
		background-position: left center;
		background-size: contain;
	    }
	.nav-row{
		flex-direction: column;
		text-align: center;
		background-color: hsl(257, 27%, 26%);
		padding: 40px;
		line-height: 2.5;
		display: none;
        z-index: 100;
		border-radius: 15px;
		text-align: center;
	    }
	.nav-line{
		display: block;
	    }
	.logo{
		display: none;
	    }
	.nav-left {
		display: block;
		flex-direction: column;
	    }
	.responsive-home{
		display: block;
	}
	.nav-left li a{
		color: #fff;
		opacity: 1;
		font-size: 1.3rem;
	    }
	.nav-menu {
		display: block;
		margin: 0; 
		padding: 0;
	    }
	.nav-menu li{
		margin-left: 0;
	    }
	.nav-right-menu{
		flex-direction: column;
		padding: 0;
	    }
	.nav-right-menu li{
		margin: 0;
	    }
	.nav-right-menu li a{
		font-size: 1.3rem;
		}
	.nav-right-menu li:nth-child(1) a{
		color: #fff;
		opacity: 9;
	    }
	.nav-right-menu li:nth-child(2){
		background-color: #0099ff;
	    border-radius: 30px;
	    padding:3px ;
	    width: 100%;
        }
	#toggle:checked + .nav-row  {
		display: block;
		text-align: center;
        }	
    }