.short{
	display: flex;
	justify-content: center;
	}
.shorten-overlay{
	flex: 1.5;
    color: white;
    background-color: #0099ff;
    border-radius: 10px 0px 0px 10px;
    padding: 16px 20px;
    font-size: 1.1rem;
    font-weight: 700;
    letter-spacing: 1px;
	margin-right: -10px;
	margin-bottom: 10.7px;
	z-index: 1000;
	text-align: center;
}
.shortener-form{
	background-image: url('../../assets/images/bg-shorten-desktop.svg');
	background-color: hsl(257, 27%, 26%);
	background-repeat: no-repeat;
	border-radius: 10px;
	border: 1px solid;
	padding: 40px;
	width: 80%;
	margin: -50px 0 0 0;
	position: absolute;
	z-index: 5;
    }

.form-control{
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
    }
.form-control input{
	flex: 5;
	border-radius: 10px;
	padding: 16px 50px;
	margin-right: 20px;	
	margin-bottom: 10px;
	font-size: 1.3rem;
	font-weight: 700;
	letter-spacing: 1px;
	z-index: 100;
	}
.input1{
	display: grid; 
	width: 70%;
}
.input2{
	display: grid; 
	width: 70%; 
	grid-auto-flow: column;
}

.btn-shorten{
	background-color: #0099ff;
	color: #fff;
	font-weight: 700;
	border: none;
	border-radius: 10px;
	padding: 16px 30px;
	letter-spacing: 1px;
	font-size: 1.3rem;	
	margin-bottom: 10px;
	width: 31%;
    }
 .btn-shorten:hover{
	background-color: #48dbfb;
    }
.errorMsg {
	color : #fdb9b9;
	text-align: center;
}
.successMsg {
	color : #10e610;
	text-align: center;
}
/*Media Queries*/
@media screen and (max-width: 900px)
{	
	.form-control{
		flex-direction: column;
	    }
	.form-control input{
		width: 100%;
		margin-right: 0;
		font-size: 1rem;
		text-align: center;
		}
	.input1{
		width: 100%;
	}
	.input2{
		width: 100%;
	}
	.btn-shorten{
	   width: 56%;
	   font-size: 1rem;
	   text-align: center;
	   }
   }
@media screen and (max-width: 587px)
{	
	.form-control input{
		padding: 16px 5px;
		letter-spacing: 0;
	}
	.shorten-overlay{
		letter-spacing: 0;
	}
}

@media screen and (max-width: 425px)
{	
	.shortener-form{
		width: 100vw;
	}
}

@media screen and (max-width: 375px)
{	
	.form-control {
		margin: 0px -20px
	}
}

@media screen and (max-width: 320px)
{	
	.form-control {
		margin: 0px -40px
	}
	
}