.link-navbar-toggle{
	display: none;
}
.res-navbar{
    display: none;
}
.overview-section{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.socials-overview{
    display: flex;    
    flex-wrap: wrap;
    justify-content: center;
}

@media screen and (max-width: 900px)
{	
	.link-navbar-toggle{
        display: block;
        padding: 2rem 2rem 0 0;
        text-align: right;
	}
    .toggle-icon{
        font-size: 35px;
    }
    .toggle-icon:hover{
        cursor: pointer;
    }
    .res-navbar{
        display: block;
        position: fixed;
        z-index: 1000;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        overflow: auto;
        background-color: rgb(0,0,0);
        background-color: rgba(0,0,0,0.4);
    }
}