.card-item-overview {
    position: relative;
    width: 18rem;
    height: 8rem;
    max-width: 100%;
    padding: 2.5rem 2rem;
    margin-right: 2rem;
    margin-bottom: 2rem;
    border-radius: 8px;
    background-color: var(--Light-Grayish-Blue);
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.card-title-overview,
.card-item-overview .card-content {
    width: 100%;
    font-weight: 700;
    font-size: var(--font-size-heading-card);
    color: var(--Light-Dark-Grayish-Blue);
    margin-bottom: 0.25rem;
    display: flex;
    justify-content: space-between;
}

.card-title-overview {
    margin-bottom: 2rem;
}

.card-item-overview .card-content {
    color: var(--Light-Very-Dark-Blue);
    align-items: flex-end;
}

.card-item-overview .card-content h2 {
    font-size: 2rem;
}

.card-item-overview .card-content p {
    color: var(--Lime-Green);
}

.card-item-overview .card-content p img {
    padding-bottom: 2px;
    padding-right: 4px;
}


/* Overview End*/
.card-item:hover,
.card-item-overview:hover {
    box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.5);
}
.card-item:active,
.card-item-overview:active {
    background-color: var(--Light-active-card-color);
}

@media only screen and (max-width: 560px), (orientation: portrait) {
    .card-item-overview {
        margin-right: 0;
    }
} 
