hr{
	width: 800px;
	height: 8px;
	background-color: #0099ff;
	border: 0;
    }
.advanced-statistics{
	background-color:#f0f1f6;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding-bottom: 60px;
    }

#statistics{
	height: 430px;
	width: 500px;
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	align-items: center;	
    }
.advanced-statistics h3{
	font-size: 35px;
	opacity: 0.8;
	margin: 20px;
	text-align: center;
    }
.advanced-statistics p{
	font-size: 15px;
	text-align: center;
	opacity: 0.3;
	font-weight: 500;
   }
.advanced-content{
	height: 400px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	border-radius: 10px;
	padding: 0;
    }

.advanced-content::before{
	content: '';
	width: 500px;
	height: 8px;
	background-color: #0099ff;
	position: absolute;
	align-self: center;
	z-index: 3;
	}
.advanced{
	width: 400px;
	margin: 20px;
	border-radius: 5px;
	background-color: #fff;
	padding: 30px;
    }
.advanced h3{
	font-size: 20px;
    }
.icon{
	height: 75px;
	width: 75px;
	border-radius: 50%;
	background-color: hsl(255, 11%, 22%);
	padding: 5px;
	margin-top: -60px;
	display: flex;
	justify-content: center;
	align-items: center;
    }
.icon img{
	height: 40px;
	width: 40px;
    }
.box1{
	display: flex;
	flex-direction: column;
	align-self: flex-start;
	z-index: 4;
    }
.box2{
	display: flex;
	flex-direction: column;
	align-self: center;
	z-index: 4;
    }
.box3{
	display: flex;
	flex-direction: column;
	align-self: flex-end;
	z-index: 4;
    }

    /*Media Queries*/
@media screen and (max-width: 900px)
{	
	.advanced-content{
	   height: auto;
	   }
	.icon{
	   margin-left: 40%;
	   }
	.advanced-content::before{
	   transform: rotate(90deg);
	   }
	.advanced{
	   margin: 30px;
	   }
	#statistics{
	   height: 400px;
	   width: 100%;
	   padding: 5px;
	   }
   }

@media screen and (max-width: 1024px) and (min-width: 768px)
{	
	.advanced-content{
		height: auto;
		}
	.advanced-content::before{
		transform: rotate(90deg);
	}
	.advanced{
		margin: 30px;
	}
}