.boost-link{
	height: 300px;
	width: 100%;
	max-width: 100%;
	background-image: url('../../assets/images/bg-boost-desktop.svg');
	background-size: cover;
	background-repeat: no-repeat;
	background-color:hsl(257, 27%, 26%);
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
    }
.boost-link h2{
	color: #fff;
	font-size: 30px;
	font-weight: 700;
	text-align: center;
    }
.btn-boost{
	background-color: #0099ff;
	border: 0;
	color: #fff;
	padding: 15px 40px;
	font-weight: 700;
	font-size: 20px;
	border-radius: 25px;
    }
.btn-boost:hover{
	background-color: #0099ff;
    }  