:root {
    /* Font families*/
    --font-family: 'Inter', sans-serif;
    /* Font families*/

    /* Font size*/
    --font-size-heading-card: 14px;
    /* Font size*/

    /* Colors Primary*/
    --Lime-Green: hsl(163, 72%, 41%);
    --Bright-Red: hsl(356, 69%, 56%);

    --Facebook: hsl(208, 92%, 53%);
    --Twitter: hsl(203, 89%, 53%);
    --Instagram: linear-gradient(
        to right,
        hsl(37, 97%, 70%),
        hsl(329, 70%, 58%)
    );
    --YouTube: hsl(348, 97%, 39%);
    /* Colors Primary*/

    /* Colors Light Theme*/
    --Light-background-color: hsl(0, 0%, 100%);
    --Light-background-top-color: hsl(225, 100%, 98%);
    --Light-Grayish-Blue: hsl(227, 47%, 96%);
    --Light-Dark-Grayish-Blue: hsl(228, 12%, 44%);
    --Light-Very-Dark-Blue: hsl(230, 17%, 14%);
    --Light-toggle-bg: hsl(230, 22%, 74%);
    --Light-active-card-color: hsl(228, 23%, 91%);
    /* Colors Light Theme*/

    /* Colors Dark Theme*/
    --Dark-background-color: hsl(230, 17%, 14%);
    --Dark-background-top-color: hsl(232, 19%, 15%);
    --Dark-Very-Desaturated-Blue: hsl(228, 28%, 20%);
    --Dark-Desaturated-Blue: hsl(228, 34%, 66%);
    --Dark-White: hsl(0, 0%, 100%);
    --Dark-active-card-color: hsl(228, 28%, 24%);
    --Dark-toggle-bg: linear-gradient(
        to right,
        hsl(210, 78%, 56%),
        hsl(146, 68%, 55%)
    );
    /* Colors Dark Theme*/
}

.card-item {
    position: relative;
    width: 18rem;
    height: 18rem;
    max-width: 100%;
    padding: 1.5rem 1rem;
    margin-right: 2rem;
    margin-bottom: 2rem;
    border-radius: 8px;
    background-color: hsl(227, 47%, 96%);
    background-color: var(--Light-Grayish-Blue);
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    padding-top: 3rem;
}

.card-item hr {
    position: absolute;
    top: 2px;
    left: 0;
    width: 100%;
    /* height: 0.3rem; */
    background-color: (--Light-Dark-Grayish-Blue);
}

.card-title {
    font-weight: 700;
    font-size: 14px;
    font-size: var(--font-size-heading-card);
    color: hsl(228, 12%, 44%);
    color: var(--Light-Dark-Grayish-Blue);
    margin-bottom: 0.25rem;
    display: flex;
    flex-direction: row;
}

.card-title h3 {
    margin-left: 0.5rem;
}

.card-item .card-content {
    padding: 1rem;
    width: 100%;
    height: 100%;
    text-align: center;
}

.card-content h2 {
    font-size: 4em;
    margin: 0;
}

.card-content h4 {
    color: hsl(228, 12%, 44%);
    color: var(--Light-Dark-Grayish-Blue);
    letter-spacing: 0.4em;
    text-transform: uppercase;
    font-weight: 400;
    margin-bottom: 2rem;
}

.card-content p {
    font-weight: 700;
    color: hsl(163, 72%, 41%);
    color: var(--Lime-Green);
}

.card-content p img {
    padding-bottom: 2px;
    padding-right: 4px;
}

@media only screen and (max-width: 560px), (orientation: portrait) {
    .card-item {
        margin-right: 0;
    }
} 

.card-item-overview {
    position: relative;
    width: 18rem;
    height: 8rem;
    max-width: 100%;
    padding: 2.5rem 2rem;
    margin-right: 2rem;
    margin-bottom: 2rem;
    border-radius: 8px;
    background-color: var(--Light-Grayish-Blue);
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.card-title-overview,
.card-item-overview .card-content {
    width: 100%;
    font-weight: 700;
    font-size: var(--font-size-heading-card);
    color: var(--Light-Dark-Grayish-Blue);
    margin-bottom: 0.25rem;
    display: flex;
    justify-content: space-between;
}

.card-title-overview {
    margin-bottom: 2rem;
}

.card-item-overview .card-content {
    color: var(--Light-Very-Dark-Blue);
    align-items: flex-end;
}

.card-item-overview .card-content h2 {
    font-size: 2rem;
}

.card-item-overview .card-content p {
    color: var(--Lime-Green);
}

.card-item-overview .card-content p img {
    padding-bottom: 2px;
    padding-right: 4px;
}


/* Overview End*/
.card-item:hover,
.card-item-overview:hover {
    box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.5);
}
.card-item:active,
.card-item-overview:active {
    background-color: var(--Light-active-card-color);
}

@media only screen and (max-width: 560px), (orientation: portrait) {
    .card-item-overview {
        margin-right: 0;
    }
} 

