.header-content{
	display: flex;
	margin-bottom: 150px;
	/* margin: 3% -10% 8% 10%; */
	padding-left: 8%;
	position: relative;
    }

.header-img {
    display: flex;
    margin-right: -50px;
    right: 0;
    justify-content: center;
	}
.header-text{
	width: 45%;
	padding: 20px;
    }
.header-text h2{
	font-size: 4rem;
	line-height:1.3;
	font-weight: 800;
	font-family: 'Poppins', sans-serif;
	margin: 0;
	opacity: 0.8;
	letter-spacing: 1px;
    }
.header-text p{
	margin: 0;
	line-height:1.5;
	padding: 0;
	font-family: 'Poppins', sans-serif;
	opacity: 0.4;
	font-weight: 500;
	font-size: 1rem;
    }
.header-text .btn-header{
	margin: 20px 20px 20px 0;
	padding: 13px 40px;
	font-size: 1rem;
	color: #fff;
	background-color: #0099ff;
	border: none;
	font-weight: 800;
	line-height: 1.2;
	border-radius: 20px;
	letter-spacing: 1px;
    }
.header-text .btn-header:hover{
	background-color: #48dbfb;
	transition: all 1s;
    } 
    
    @media screen and (max-width: 900px)
{	
	.header-content{
		flex-direction: column-reverse;
		justify-content: center;
		align-items: center;
		padding: 30px;
		margin: 0;
	    }
	.header-text{
		width: 100%;
		padding: 20px;
	    }
	.header-img{
		width: 100%;
	    }
	.header-text h2{
		font-size: 2rem;
		font-weight: 700;
	    }
   }