footer{
    background-color:hsl(260, 8%, 14%);
    bottom: 0;
    /* position: absolute; */
    left: 0;
    right: 0;
    /* z-index: -1; */
    }
.footer{
	display: flex;
	justify-content: space-evenly;
	flex-wrap: wrap;
	padding: 40px;
    }
.footer-logo h3{
    color: #fff;
    font-size: 35px;
    margin-top: 0;
    margin-bottom: 0;
    }
.footer-text{	
	display: flex;
	flex-wrap: wrap;	
    }
.footer-section ul{
	list-style-type: none;
    }
.footer-section ul li a{
	color:  hsl(257, 7%, 63%);
	text-decoration: none;
	font-size: 18px;
    }
.footer-section ul li:nth-child(1){
	color: #fff;		
    }
.social-details ul{
	display: flex;
    }
.social-details img{
	margin-left: 15px;
    }